.generic-slider {
    $this: &;

    max-width: 100%;

    &--with-filters {
        margin-top: -42px;

        @include until-desktop {
            margin-top: 0;
        }

        @include desktop-medium {
            margin-top: 0;
        }
    }

    &__head {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 31px;

        #{$this}--with-filters & {
            @include until-desktop {
                margin-top: 30px;
            }

            @include desktop-medium {
                margin-bottom: 31px;
            }
        }

        #{$this}--without-filters & {
            align-items: flex-start;
        }
    }

    &__inner {
        @include until-desktop {
            display: flex;
            flex-direction: column;
            width: 100%;
        }

        #{$this}--image-gallery & {
            padding-top: 76px;
            padding-bottom: 76px;

            @include desktop-medium {
                padding-top: 118px;
                padding-bottom: 106px;
            }
        }

        #{$this}--without-filters & {
            padding-top: 70px;
            padding-bottom: 68px;

            @include desktop-medium {
                padding-top: 91px;
                padding-bottom: 106px;
            }
        }


        #{$this}--with-filters & {
            margin-right: 0;
            margin-left: 0;
        }

    }

    &__controls {
        grid-template-columns: auto auto;
        display: grid;
        width: max-content;
        gap: 20px;
        margin-left: auto;

        #{$this}--image-gallery & {
            margin-top: 30px;

            @include desktop-medium {
                margin-top: 60px;
                margin-bottom: 0;
            }
        }

        #{$this}--with-filters & {
            @include desktop-medium {
                margin-top: 9px;
            }
        }

        #{$this}--without-filters & {
            margin-top: 7px;

            @include desktop-medium {
                margin-top: 9px;
            }
        }

        @include desktop {
            margin-top: 0;
            margin-bottom: 20px;
        }
    }

    &__title {
        font-family: $ff-financier-display;
        font-size: 30px;
        font-weight: $fw-light;
        line-height: 1.2;

        @include desktop-medium {
            font-size: 40px;
        }

        #{$this}--with-filters & {
            display: none;
        }
    }

    &__slider-container {
        max-width: 100%;
        margin-right: auto;
        margin-left: auto;
        //overflow: hidden;

        @include tablet {
            max-width: 100%;
        }
    }

    &__list {
        display: flex;
        width: 100%;
        flex-wrap: nowrap;
        backface-visibility: hidden;
        transform-style: preserve-3d;
        list-style: none;
        touch-action: pan-Y;

        #{$this}--without-filters &,
        #{$this}--image-gallery & {
            align-items: center;
        }
    }

    &__card {
        box-sizing: content-box;
        padding-right: 20px;
        min-width: 304px;
        max-width: 304px;
        width: 300px;

        @include tablet {
            min-width: 389px;
            max-width: 389px;
        }

        @include desktop-medium {
            padding-right: 40px;
        }

        &:nth-child(odd) {
            #{$this}--without-filters &,
            #{$this}--image-gallery & {
                min-width: 258px;
                max-width: 258px;

                @include desktop-medium {
                    min-width: 532px;
                    max-width: 532px;
                }
            }
        }

        &:nth-child(even) {
            #{$this}--without-filters &,
            #{$this}--image-gallery & {
                min-width: 320px;
                max-width: 320px;

                @include desktop-medium {
                    min-width: 675px;
                    max-width: 675px;
                }
            }
        }

        #{$this}--without-filters &,
        #{$this}--image-gallery & {
            height: fit-content;
        }

        #{$this}--with-filters & {
            &:first-child {
                @include desktop {
                    min-width: 818px;
                    max-width: 818px;
                }
            }
        }
    }

    &--without-filters {
        background-color: $color-beige;
    }
}
